/* eslint-disable no-underscore-dangle */
import getImage from '../scripts/data/config';

class Card extends HTMLElement {
  set card(data) {
    this._card = data;
    this.render();
    // this.cardAction()
  }

  cardAction = () => {
    const button = this.querySelector('.card-button');
    const card = this.querySelector('.card');
    button.setAttribute('aria-label', `show detail for ${this._card.name}`);

    button.addEventListener('click', () => {
      card.classList.toggle('fit-content');
      if (card.classList.contains('fit-content')) {
        button.innerHTML = 'Hide';
        button.setAttribute('aria-label', `hide detail for ${this._card.name}`);
      } else {
        button.innerHTML = 'Show';
        button.setAttribute('aria-label', `show detail for ${this._card.name}`);
      }
    });
  };

  render() {
    this.innerHTML = `
            <div class="card">
                <figure class="card-header">
                    <img src="${getImage.API_GET_IMAGE('small', this._card.pictureId)}" alt="image-of-${this._card.name}" loading="lazy">
                </figure>
                <span aria-label="rating ${this._card.rating}"><i class="bi bi-star"></i>${this._card.rating}</span>
                <div class="card-body">
                    <div class="title">
                        <h3>${this._card.name}</h3>
                        <p><i class="bi bi-geo-alt"></i>${this._card.city}</p>
                    </div>
                    <a href="#/detail/${this._card.id}" class="card-button">Show</a>
                    <div class="description">
                        <p class="description-text">${String(this._card.description).substring(0, 250)}...</p>
                    </div>
                </div>
            </div>
            `;
  }
}

customElements.define('card-tag', Card);
