class Navbar extends HTMLElement {
  connectedCallback() {
    this.render();
    this.menuAction();
  }

  menuAction = () => {
    const button = this.querySelector('.menu-button');
    const menu = this.querySelector('.menu');
    const main = document.querySelector('main');

    if (window.innerWidth > '600') {
      button.classList.add('hidden');
      menu.classList.remove('hidden');
    }

    if (window.innerWidth < '600') {
      main.addEventListener('click', () => {
        if (!menu.classList.contains('hidden')) {
          menu.classList.add('hidden');
        }
      });
    }

    button.addEventListener('click', () => {
      menu.classList.toggle('hidden');
    });
  };

  render() {
    this.innerHTML = `
        <nav>
            <div class="logo">
                <h1>DRCataloge</h1>
            </div>
            <button class="menu-button" aria-label="list-menu">
                <i class="bi bi-list"></i>
            </button>
            <ul class="menu hidden">
                <li><a href="/">Home</a></li>
                <li><a href="#/favorite">Favorite</a></li>
                <li><a href="https://www.linkedin.com/in/riiifkiii/" target="_blank" rel="noreferrer">About Us</a></li>
            </ul>
        </nav>
        `;
  }
}

customElements.define('nav-tag', Navbar);
