/* eslint-disable array-callback-return */
import './Card';

class ListRestaurant extends HTMLElement {
  set results(results) {
    this._result = results;
    this.render();
  }

  render() {
    if (this._result.length < 1) {
      this.innerHTML = '<h1 class="restaurant-empty">Tidak ada restaurant yang disukai</h1>';
      return;
    }

    this._result.map((m) => {
      const cardTag = document.createElement('card-tag');
      cardTag.card = m;
      this.appendChild(cardTag);
    });
  }
}

customElements.define('list-restaurant', ListRestaurant);
