// import heroImage2 from '../public/images/heros/hero-image_2.jpg';

class Hero extends HTMLElement {
  connectedCallback() {
    this.render();
  }

  render() {
    this.innerHTML = `
            <h2>Dicoding Restaurant Cataloge</h2>
            <picture>
              <source media="(max-width: 425px)" srcset="./images/hero-image-small.jpg">
              <source media="(max-width: 768px)" srcset="./images/hero-image-medium.jpg">
              <img src="./images/heros/hero-image.jpg" alt="hero-image-cooking">
            </picture>
        `;
  }
}

customElements.define('hero-tag', Hero);
